	module.exports = {"data":{"markdownRemark":{"html":"<h2>Empathy Maps</h2>\n<p>Empathy maps are an important tool to use to introduce and humanize customers when presenting user research. They are used internally to create a deeper understanding of customers. </p>\n<p>A typical empathy map presents basic information about the interviewee and separates research visually into four quadrants— what users think, do, say, and feel. Below is an example.\n<img src=\"https://www.webtexttool.com/wp-content/uploads/2015/05/The-Empathy-Map.png\" alt=\"alt text\"></p>\n<h4>More Information:</h4>\n<p><a href=\"https://uxmag.com/articles/how-to-use-persona-empathy-mapping\">How to Use Persona Empathy Mapping</a>\n<a href=\"https://uxdict.io/design-thinking-empathy-map-c69ab5d6b22\">Design Thinking: Empathy Maps</a></p>","fields":{"slug":"/product-design/empathy-maps/"},"frontmatter":{"title":"Empathy Maps"}}},"pathContext":{"slug":"/product-design/empathy-maps/"}}

